import styleInject from '#style-inject';styleInject(".fire-text-container{display:inline-block;position:relative}.fire-text{display:inline-block;font-weight:700;color:#fff;text-shadow:0 0 10px #ff6b00,0 0 20px #ff6b00,0 0 30px #ff6b00,0 0 40px #ff4500,0 0 70px #ff4500,0 0 80px #ff4500,0 0 100px #ff4500,0 0 150px #ff4500;animation:fire-flicker 1.5s infinite alternate;position:relative}.fire-text:before{content:attr(data-text);position:absolute;left:0;top:0;color:red;text-shadow:0 0 10px #ff0000,0 0 20px #ff4500,0 0 30px #ff6b00;animation:fire-pulse .5s infinite alternate;z-index:-1}.fire-text:after{content:attr(data-text);position:absolute;left:0;top:0;color:#ff0;text-shadow:0 0 5px #ffff00,0 0 10px #ffa500;animation:fire-wave 1s infinite alternate;z-index:-2}@keyframes fire-flicker{0%,to{text-shadow:0 0 10px #ff6b00,0 0 20px #ff6b00,0 0 30px #ff6b00,0 0 40px #ff4500,0 0 70px #ff4500,0 0 80px #ff4500,0 0 100px #ff4500}50%{text-shadow:0 0 5px #ff6b00,0 0 15px #ff6b00,0 0 20px #ff6b00,0 0 30px #ff4500,0 0 50px #ff4500,0 0 60px #ff4500,0 0 80px #ff4500}}@keyframes fire-pulse{0%{opacity:.8;transform:scale(1)}to{opacity:1;transform:scale(1.02)}}@keyframes fire-wave{0%{opacity:.5;transform:translateY(0)}to{opacity:.8;transform:translateY(-2px)}}\n")